/* STRUCTDATA TABLE */
.table {
    &__structure {
        .fa {
            margin-right: 10px;
        }
        a {
            color: @black;
            text-decoration: none;
        }
        .selected {
            font-weight: 700;
            color: @btnSuccess;
        }
