/* MODIFICATIONS */
.table__structure-link-active {
	font-family: @osBold;
}
.table__structure-popover-ul {
	li:nth-child(odd) {
		font-family: @osBold;	
	}
}

/* SELECT RADIO */
table {
    &.select-radio {
        td {
            text-align: left;
        }
        label {
            font-family: @osRegular;
            font-size: 1rem;
        }
    }
}

